@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

/*      */
.LoginPage {
    background-color: #2b2b2b;
    width: 428px;
    height: 926px;
    overflow: hidden;
    text-align: center;
    color: #ffffff;
    position: absolute;
    top: calc(50% - 463px);
    left: calc(50% - 214px);
    transform-origin: center; /*     */
    transform: scale(1); /*    1  */
}

/*    */
.HeaderIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    stroke: #ffffff;
    top: 87px;
    left: 32px;
}

/*    */
.HeaderTitle {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 85px;
    left:180px;
}

/*     */
.LoginPageInput {
    position: relative;
    top: 245px;
    left: 44px;
    margin-bottom: 49px;
}

.LoginPageInput input {
    font-size: 25px; /*      */
    padding: 10px 10px 10px 5px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
    display: block;
    width: 340px;
    height: 70px;
    border: none;
    color: #ffffff;
    border-bottom: 1px solid #757575;
    outline: none; /*         */
}

.LoginPageInput input::placeholder {
    text-align: center;
    font-size: 18px;
}

/*       */
.LoginPageInput label {
    color:#999;
    font-size:10px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
    opacity: 0;
}

.LoginPageInput img{
    position: absolute;
    left: 16px;
    opacity: 1; /*    */
    transition:0.2s ease all; /*      */
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
}

/*           */
.LoginPageInput input:focus ~ img,
.LoginPageInput input:valid ~ img{
    opacity: 0;
}


.passicon{
    top: 23px;
}

.emailicon{
    top: 26px;
}


/*              */
.LoginPageInput input:focus ~ label, input:valid ~ label {
    top:-30px;
    font-size:20px;
    color:#D6B4A2;
    opacity: 1;
}

/*      */
.bar {
    position: relative;
    display: block;
    width: 328px;
}

.bar:before,
.bar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #D6B4A2;
    transition: 0.2s ease all;
    -moz-transition: 0.2s ease all;
    -webkit-transition: 0.2s ease all;
}

.bar:before {
    left: 50%;
}

.bar:after {
    right: 50%;
}

/*         */
input:focus ~ .bar:before,
input:focus ~ .bar:after {
    width: calc(50% - 3%);
}

/*        */
.highlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/*     */
input:focus ~ .highlight {
    -webkit-animation: inputHighlighter 0.3s ease;
    -moz-animation: inputHighlighter 0.3s ease;
    animation: inputHighlighter 0.3s ease;
}

/*    */
@keyframes inputHighlighter {
    from {
        background: #D6B4A2;
    }
    to {
        width: 0;
        background: transparent;
    }
}

/*     */
.LoginPageCheck {
    position: absolute;
    top: 443px;
    left: 52px;
}

/*     */
.LoginPageLink {
    position: absolute;
    top: 443px;
