/*    */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

.SignUpBackgroundColor{
    background-color: #ffffff;
    width: 100vw;
    height: 100vh;
}

/*    */
.SignUpPage{
    background-color: #ffffff;
    width: 428px;
    height: 926px;
    overflow: hidden;
    text-align: center;
    color: #000000;
    position: absolute;
    top: calc(50% - 463px);
    left: calc(50% - 214px);
    transform-origin: center; /*     */
    transform: scale(1); /*    1  */
}

/*    */
.SignUpIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    top: 87px;
    left: 32px;
}

/*    */
.SignUpTitle {
    position: absolute;
    color: #000000;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 85px;
    left:180px;
}

/*    */
.SignUpPageInput {
    position: relative;
    top: 245px;
    left: 44px;
    margin-bottom: 30px;
}

.SignUpPageInput input {
    font-size: 25px;
    padding: 10px 10px 10px 5px;
    border-radius: 15px;
    background: rgba(217, 217, 217, 0.20);
    display: block;
    width: 340px;
    height: 70px;
    border: none;
    color: #000000;
    border-bottom: 1px solid #757575;
    outline: none;
}

/*      */
.SignUpPageInput input::placeholder {
    text-align: center;
    font-size: 18px;
}

/*   */
.SignUpPageInput label {
    color:#999;
    font-size:10px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:5px;
    top:10px;
    transition:0.2s ease all;
    opacity: 0;
}

/*    */
.SignUpPageInput img{
    position: absolute;
    left: 16px;
    opacity: 1;
    transition:0.2s ease all;
}

/*           */
.SignUpPageInput input:focus ~ img,
.SignUpPageInput input:valid ~ img{
    opacity: 0;
}

/*     */
.UserIcon, .PassIcon, .PhoneIcon{
    top: 23px;
}

.EmailIcon{
    top: 26px;
}

/*              */
.SignUpPageInput input:focus ~ label, input:valid ~ label {
    top:-30px;
    font-size:20px;
    color: #B24C4B;
    opacity: 1;
}

/*      */
.SignUpBar {
    position: relative;
    display: block;
    width: 328px;
}

.SignUpBar:before,
.SignUpBar:after {
    content: '';
    height: 2px;
    width: 0;
    bottom: 1px;
    position: absolute;
    background: #B24C4B;
    transition: 0.2s ease all;
}

.SignUpBar:before {
    left: 50%;
}

.SignUpBar:after {
    right: 50%;
}

/*         */
input:focus ~ .SignUpBar:before,
input:focus ~ .SignUpBar:after {
    width: calc(50% - 3%);
}

/*        */
.SignUpHighlight {
    position: absolute;
    height: 60%;
    width: 100px;
    top: 25%;
    left: 0;
    pointer-events: none;
    opacity: 0.5;
}

/*    */
input:focus ~ .SignUpHighlight {
    animation: inputHighlighter 0.3s ease;
}

/*    */
@keyframes inputHighlighter {
    from {
        background: #7d7d7d;
    }
    to {
        width: 0;
        background: transparent;
    }
}

/*    */
.SignUpPageLink{
    position: absolute;
    top: 630px;
    left: 65px;
    /*width: 340px;*/
    /*height: 70px;*/
    /*border-radius: 15px;*/
    /*background: #D6B4A2;*/
}

.SignUpPageLinkText1 {
    color: #000000;
}

.SignUpPageLinkText2 {
    color: #ffffff;
}

.SignUpPageLinkText3 {
    text-decoration: underline;
    /*text-align: center;*/
    /*font-family: 'Roboto';*/
    /*font-size: 20px;*/
    /*font-weight: 600;*/
    /*line-height: 70px;*/
    /*color: black;*/
    color: #B24C4B;
    cursor: pointer;

}

/*    */
.SignUpButton {
    position: absolute;
    top: 730px;
    left: 44px;
    width: 340px;
    height: 70px;
    border-radius: 15px;
    background: #D6B4A2;
}

/*     */
.SignUpButton span {
    position: absolute;
    top: 21px;
    left: 136px;
    color: #2b2b2b;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 600;
    line-height: normal;
}

/*     */
.SingUpPageFooter{
    position: absolute;
    top: 830px;
    left: 129px;
}

.SingUpFooterText1 {
    color: #000000;
}

.SingUpFooterText2 {
    color: #ffffff;
}

.SingUpFooterText3 {
    text-decoration: underline;
    color: #B24C4B;
}

.MsgIcon{
    width: 23px;
    margin-bottom: 5px;
}
.RedCrossIcon{
    position: absolute;
    width: 25px;
    margin-bottom: 5px;
    bottom: 55px;
    left: 280px;
}
.GreenCheckIcon{
    position: absolute;
    width: 25px;
    margin-bottom: 5px;
    bottom: 55px;
    left: 280px;
}

.SignUpAuthButton{
    cursor: pointer;
    margin-left: 30px;
}

/*    428px     */
@media screen and (max-width: 428px) {
    .SignUpPage {
        transform: scale(0.82); /*  0.82  82%  */
    }
}
