/*    */
@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;600&display=swap");

/*    */
.FindPassPage{
    background-color: #2B2B2B;
    width: 428px;
    height: 926px;
    overflow: hidden;
    text-align: center;
    color: #ffffff;
    position: absolute;
    top: calc(50% - 463px);
    left: calc(50% - 214px);
    transform-origin: center; /*     */
    transform: scale(1); /*    1  */
}

/*    */
.FindPassIcon {
    position: absolute;
    width: 32.143px;
    height: 25px;
    flex-shrink: 0;
    stroke-width: 6px;
    top: 87px;
    left: 32px;
}

/*    */
.FindPasspTitle {
    position: absolute;
    color: #ffffff;
    text-align: center;
    font-family: 'Roboto';
    font-size: 25px;
    font-weight: 500;
    line-height: normal;
    top: 85px;
    left:142px;
}

/*    */
.FindPassHeaderText{
