.RedirectPage {
    background-color: #2b2b2b;
    width: 428px;
    height: 926px;
    overflow: hidden;
    text-align: center;
    color: #ffffff;
    position: absolute;
    top: calc(50% - 463px);
    left: calc(50% - 214px);
    transform-origin: center; /*     */
    transform: scale(1); /*    1  */
}

